import { letterToNum } from '@/constants/globalDatas';
import { ProviderContext } from '@/reducer/context';
import getUrlParams from '@/utils/get-url-params';
import { Button, Carousel, Icon } from 'antd-mobile';
import React, { ReactElement, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import {
  Page,
  HeaderBox,
  GrayFont,
  FontContent,
  CarouselItem,
  CarouseBox,
  ButtonBox,
  ButtonBoxInner,
  ParsingFont,
  HeaderBlank,
  HeaderInner,
  ButtonBlank,
} from '../../styles';
import getMathpix from '../../utils/getMathpix';
import { AnswerBox, AnswerItem } from '../styles';

const Parsing: React.FC = () => {
  const history = useHistory();
  const { tag } = getUrlParams();
  const { state } = useContext(ProviderContext);
  const { quick, selectAnswer, quickTrueAnswer } = state;

  // 获得解析列表
  const getParsingList = (): ReactElement[] => {
    return (
      quick?.map((item, index) => {
        const { questionId, question, parse, options } = item;
        const trueAnswer = quickTrueAnswer?.[index];
        const { answer } = trueAnswer || {};
        const selectAs = selectAnswer?.[index];
        return (
          <CarouselItem key={questionId}>
            <HeaderBox className="lr-pd">
              <GrayFont>{tag}</GrayFont>
              <GrayFont>
                {index + 1}/{quick.length}
              </GrayFont>
            </HeaderBox>
            <FontContent>{getMathpix(question)}</FontContent>
            <AnswerBox>
              {options.map((val, idx) => {
                const isTrue = answer === selectAs;
                const selectNum =
                  letterToNum[selectAs as keyof typeof letterToNum];
                const trueNum = letterToNum[answer as keyof typeof letterToNum];
                if (isTrue) {
                  return (
                    <AnswerItem
                      className={selectNum === idx ? 'active' : ''}
                      key={val}
                    >
                      {val}
                    </AnswerItem>
                  );
                }
                if (selectNum === idx) {
                  return (
                    <AnswerItem className="error" key={val}>
                      {val}
                    </AnswerItem>
                  );
                }
                if (trueNum === idx) {
                  return (
                    <AnswerItem className="active" key={val}>
                      {val}
                    </AnswerItem>
                  );
                }
                return <AnswerItem key={val}>{val}</AnswerItem>;
              })}
            </AnswerBox>
            <ParsingFont>
              <div className="title">正确答案：{answer}</div>
              解析：
              <br />
              {getMathpix(parse)}
            </ParsingFont>
          </CarouselItem>
        );
      }) || []
    );
  };
  return (
    <Page>
      <HeaderBox>
        <HeaderBlank />
        <HeaderInner>
          <Icon
            type="cross"
            color="#262626"
            onClick={(): void => history.go(-2)}
          />
        </HeaderInner>
      </HeaderBox>
      <CarouseBox>
        <Carousel dots={false} style={{ height: '100%' }}>
          {getParsingList()}
        </Carousel>
      </CarouseBox>
      <ButtonBox>
        <ButtonBlank />
        <ButtonBoxInner>
          <Button type="primary" onClick={(): void => history.go(-1)}>
            返回报告
          </Button>
        </ButtonBoxInner>
      </ButtonBox>
    </Page>
  );
};

export default Parsing;
